* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: #0a0a0a;
  color: #fff;
  overflow-x: hidden;
  height: 100vh;
}

/* Neon text effect */
.neon {
  text-shadow: 
    0 0 10px #FF1744,
    0 0 20px #FF1744,
    0 0 30px #FF1744,
    0 0 40px #FF1744;
  animation: flicker 2s infinite;
}

@keyframes flicker {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.8; }
}

/* Landing page */
.landing {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 2rem;
  text-align: center;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);
}

.landing h1 {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  line-height: 1.2;
}

.landing p {
  font-size: 1.2rem;
  opacity: 0.9;
  margin-bottom: 3rem;
  max-width: 500px;
}

.btn-primary {
  background: #FF1744;
  color: white;
  border: none;
  padding: 1rem 3rem;
  font-size: 1.2rem;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s;
  box-shadow: 0 4px 20px rgba(255, 23, 68, 0.5);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 30px rgba(255, 23, 68, 0.7);
}

/* Compass screen */
.compass-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: #0a0a0a;
}

.compass-header {
  padding: 1.5rem;
  text-align: center;
  background: rgba(255, 255, 255, 0.05);
}

.compass-header h2 {
  font-size: 1.5rem;
  color: #FF1744;
}

.compass-main {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.compass {
  width: 280px;
  height: 280px;
  position: relative;
  border: 3px solid #FF1744;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(255, 23, 68, 0.1) 0%, transparent 70%);
  box-shadow: 0 0 40px rgba(255, 23, 68, 0.5);
}

.compass-arrow {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: transform 0.3s ease-out;
}

.arrow-shape {
  position: absolute;
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-bottom: 80px solid #FF1744;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
  filter: drop-shadow(0 0 10px rgba(255, 23, 68, 0.8));
}

.distance-display {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  font-weight: bold;
  text-shadow: 0 0 20px rgba(255, 23, 68, 0.8);
}

.compass-footer {
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  text-align: center;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
  margin-top: 1rem;
}

.progress-fill {
  height: 100%;
  background: #FF1744;
  transition: width 0.3s;
  box-shadow: 0 0 10px rgba(255, 23, 68, 0.8);
}

/* Badge modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.badge-modal {
  background: #1a1a1a;
  padding: 2rem;
  border-radius: 20px;
  text-align: center;
  max-width: 90%;
  animation: slideUp 0.5s;
  border: 2px solid #FF1744;
  box-shadow: 0 0 40px rgba(255, 23, 68, 0.5);
}

@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.badge-icon {
  width: 200px;
  height: 200px;
  margin: 1rem 0;
  font-size: 8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: bounce 1s;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-30px); }
  60% { transform: translateY(-15px); }
}

.confetti {
  position: absolute;
  width: 10px;
  height: 10px;
  background: #FF1744;
  animation: confettiFall 3s linear;
  pointer-events: none;
}

@keyframes confettiFall {
  to {
    transform: translateY(100vh) rotate(360deg);
    opacity: 0;
  }
}

/* Final form */
.final-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 2rem;
  text-align: center;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);
}

.larry-illustration {
  font-size: 10rem;
  margin-bottom: 2rem;
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.email-form {
  width: 100%;
  max-width: 400px;
}

.email-input {
  width: 100%;
  padding: 1rem;
  font-size: 1.1rem;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid #FF1744;
  border-radius: 10px;
  color: white;
  margin-bottom: 1rem;
}

.email-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

/* Voucher screen */
.voucher-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 2rem;
  text-align: center;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);
}

.voucher-code {
  font-family: 'Courier New', monospace;
  font-size: 2rem;
  background: rgba(255, 23, 68, 0.1);
  padding: 1.5rem 2rem;
  border-radius: 10px;
  border: 2px solid #FF1744;
  margin: 2rem 0;
  letter-spacing: 2px;
}

.countdown {
  font-size: 1.2rem;
  opacity: 0.8;
  margin-bottom: 2rem;
}

.share-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

.share-btn {
  padding: 0.8rem 1.5rem;
  border: 2px solid #FF1744;
  background: transparent;
  color: #FF1744;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s;
}

.share-btn:hover {
  background: #FF1744;
  color: white;
}

/* Add these styles to your existing App.css */

/* Loading Screen */
.loading-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);
}

.loading-content {
  text-align: center;
}

.loading-spinner {
  width: 60px;
  height: 60px;
  border: 3px solid rgba(255, 23, 68, 0.3);
  border-top-color: #FF1744;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 2rem;
}

/* Error Screen */
.error-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);
  padding: 2rem;
}

.error-content {
  text-align: center;
  max-width: 400px;
}

.error-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.error-content h2 {
  color: #FF1744;
  margin-bottom: 1rem;
}

.error-content p {
  margin-bottom: 2rem;
  opacity: 0.8;
}

/* GPS Indicator */
.gps-indicator {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  margin-top: 0.5rem;
  position: relative;
}

.gps-dot {
  width: 8px;
  height: 8px;
  background: white;
  border-radius: 50%;
  display: inline-block;
  animation: pulse 2s infinite;
}

.accuracy-text {
  font-size: 0.8rem;
  opacity: 0.8;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.5);
    opacity: 0.5;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Compass Enhancements */
.compass-rose {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.compass-direction {
  position: absolute;
  font-weight: bold;
  font-size: 1.2rem;
  opacity: 0.5;
}

.compass-direction.north {
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  color: #FF1744;
  opacity: 0.8;
}

.compass-direction.east {
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.compass-direction.south {
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}

.compass-direction.west {
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
}

/* Distance Display Enhancements */
.distance-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.distance-number {
  font-size: 3rem;
  font-weight: bold;
  line-height: 1;
}

.distance-unit {
  font-size: 1rem;
  opacity: 0.8;
}

.distance-loading {
  position: relative;
  width: 60px;
  height: 60px;
}

.pulse-ring {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 2px solid #FF1744;
  border-radius: 50%;
  animation: pulseRing 2s cubic-bezier(0.455, 0.03, 0.515, 0.955) infinite;
}

.pulse-ring:nth-child(2) {
  animation-delay: 0.5s;
}

.pulse-ring:nth-child(3) {
  animation-delay: 1s;
}

@keyframes pulseRing {
  0% {
    transform: scale(0.5);
    opacity: 1;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}

.distance-hint {
  margin-top: 1rem;
  font-size: 1.1rem;
  opacity: 0.9;
  min-height: 1.5rem;
}

/* Debug Panel */
.debug-panel {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.95);
  border-top: 2px solid #FF1744;
  max-height: 50vh;
  overflow-y: auto;
  z-index: 2000;
  animation: slideUp 0.3s;
}

.debug-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.debug-header h3 {
  margin: 0;
  color: #FF1744;
}

.debug-close {
  background: none;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.debug-content {
  padding: 1rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.debug-item {
  background: rgba(255, 255, 255, 0.05);
  padding: 0.75rem;
  border-radius: 8px;
  font-size: 0.9rem;
}

.debug-item strong {
  color: #FF1744;
  display: block;
  margin-bottom: 0.25rem;
}

/* Responsive adjustments */
@media (max-width: 380px) {
  .compass {
    width: 240px;
    height: 240px;
  }
  
  .distance-number {
    font-size: 2.5rem;
  }
  
  .compass-direction {
    font-size: 1rem;
  }
}

/* Badge Modal Enhancement */
.badge-success-message {
  font-size: 1.1rem;
  opacity: 0.9;
  margin-top: 1rem;
}

/* Vibration feedback visual indicator */
@keyframes vibrate {
  0%, 100% { transform: translateX(0); }
  10%, 30%, 50%, 70%, 90% { transform: translateX(-2px); }
  20%, 40%, 60%, 80% { transform: translateX(2px); }
}

.vibrating {
  animation: vibrate 0.5s;
}

/* Add these to your App.css */

/* Enhanced Badge Modal */
.badge-modal {
  position: relative;
  overflow: visible;
}

.badge-modal.celebrating {
  animation: celebrateScale 0.5s ease-out;
}

@keyframes celebrateScale {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.badge-glow {
  position: absolute;
  top: -50%;
  left: -50%;
  right: -50%;
  bottom: -50%;
  background: radial-gradient(circle, rgba(255, 23, 68, 0.3) 0%, transparent 70%);
  animation: glowPulse 2s ease-in-out infinite;
  pointer-events: none;
}

@keyframes glowPulse {
  0%, 100% {
    opacity: 0.5;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
}

.badge-title {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  animation: titleBounce 0.5s ease-out 0.2s both;
}

@keyframes titleBounce {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.badge-container {
  position: relative;
  margin: 2rem 0;
}

.ingredient-icon {
  position: relative;
  z-index: 1;
  animation: iconFloat 3s ease-in-out infinite;
}

@keyframes iconFloat {
  0%, 100% {
    transform: translateY(0) rotate(-5deg);
  }
  50% {
    transform: translateY(-10px) rotate(5deg);
  }
}

.badge-sparkles {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150%;
  height: 150%;
  pointer-events: none;
}

.sparkle {
  position: absolute;
  font-size: 1.5rem;
  animation: sparkleFloat 3s ease-in-out infinite;
}

.sparkle:nth-child(1) {
  top: 0;
  left: 20%;
  animation-delay: 0s;
}

.sparkle:nth-child(2) {
  top: 20%;
  right: 10%;
  animation-delay: 0.5s;
}

.sparkle:nth-child(3) {
  bottom: 20%;
  left: 10%;
  animation-delay: 1s;
}

.sparkle:nth-child(4) {
  bottom: 0;
  right: 20%;
  animation-delay: 1.5s;
}

@keyframes sparkleFloat {
  0%, 100% {
    transform: translateY(0) scale(1);
    opacity: 0;
  }
  50% {
    transform: translateY(-20px) scale(1.2);
    opacity: 1;
  }
}

.ingredient-name {
  font-size: 1.5rem;
  margin: 1rem 0;
  color: #FF1744;
}

.collected-indicators {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin: 2rem 0;
}

.indicator {
  width: 40px;
  height: 40px;
  border: 2px solid #FF1744;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  background: rgba(255, 23, 68, 0.1);
  transition: all 0.3s;
}

.indicator.just-collected {
  animation: indicatorPop 0.5s ease-out;
  background: #FF1744;
  color: white;
}

@keyframes indicatorPop {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.next-button {
  margin-top: 1.5rem;
  animation: buttonSlideUp 0.5s ease-out 0.4s both;
}

@keyframes buttonSlideUp {
  0% {
    transform: translateY(20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}